.background {
  height: 680px;
  width: 1060px;
  background: linear-gradient(45deg, #294954, #4c5a43);
  display: flex;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  align-items: center;
}

.box {
  height: 500px;
  width: 400px;
  background: linear-gradient(45deg, #008cb9, #97cf6c);
  display: flex;
  justify-content: flex-end;
  box-shadow: 0 15px 30px 5px;
}

.tennis {
  height: 480px;
  width: 520px;
  transform: rotate(35deg) scaleX(-1);
}

.menu {
  height: 560px;
  width: 400px;
  background: white;
  box-shadow: 0 15px 30px -10px;
}
